<template>

        <div class="am-panel am-panel-default" :class="'panel-'+index">
            <div class="am-panel-hd ">
                <div class="cover-wrap">
                    <div class="cover-bar">
                        <div class="cover-img center-img" :style="'background-image:url('+ data.house_img.cover[0].get + ')' ">

                        </div>
                    </div>
                </div>
                <div class="am-text-left hd-text">
                    <span>{{data.city}} {{data.community}} {{data.building_number}} {{data.unit_number}} {{data.house_number}}</span>
                    <a v-if="getStatus != false" class="am-badge am-fr am-badge-secondary am-radius">{{getStatus[data.status]}}</a>
                </div>
            </div>
        <div :id="'amdoc-'+index" class="am-panel-collapse am-collapse">
            <div class="am-panel-bd">
                <div>
                    <div>
                        {{data.created_at}}
                    </div>
                    <div>
                         {{data.contact}}  {{data.tel}}
                    </div>
                    <div>
                        {{data.location}}
                    </div>
                    <div>
                        {{data.community}}
                    </div>
                    <div>
                         
                        <span v-if="data.room_count.hall >0"> {{data.room_count.hall}} </span>
                        <span v-if="data.room_count.bedroom >0"> {{data.room_count.bedroom}} </span>
                        <span v-if="data.room_count.bathroom >0"> {{data.room_count.bathroom}}     </span>
                        <span v-if="data.room_count.belcony >0"> {{data.room_count.belcony}} </span>
                        <span v-if="data.room_count.kitchen >0"> {{data.room_count.kitchen}} </span>
                    </div>
                    <div>
                        <span>
                            {{data.Decoration}}
                        </span>
                        <span class="am-margin-horizontal-sm">
                            {{data.supply_heating}}
                        </span>
                        <span class="am-margin-horizontal-sm">
                            {{data.elevator}}
                        </span>
                        <span class="am-margin-horizontal-sm">
                            {{data.direction}}
                        </span>
                    </div>
                    <div>

                    </div>
                    <div v-if="data.status == 'audit'" class="status-tip am-text-center">
                        ,.
                    </div>
                    <div v-else-if="data.status == 'pass'" class="status-tip am-text-center">
                        <router-link to="">
                            
                        </router-link>
                    </div>
                    <div v-else-if="data.status == 'sell'" class="status-tip am-text-center">
                        <router-link :to="'/house/'+data.id">
                            .
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
        <div class="am-panel-hd pft am-text-center">
            <span class="am-panel-title am-collapsed" @click="collapse(index)">
                 <i class="am-icon-angle-down" :class=""></i>
            </span>
        </div>
    </div>

</template>

<script>
   export default {
      name:'temp-commissioned',
      props:['data','index'],
      methods:{
         collapse(index){
            let collapse = $('#amdoc-'+index);
            this.toggleClass(collapse.hasClass('am-in'));
            collapse.collapse('toggle');
         },
         toggleClass(condi){
            $('.panel-'+this.index).find('i[class^=am-icon]').removeClass().addClass(condi ? 'am-icon-angle-down' : 'am-icon-angle-up');
         }
      },
      computed:{
        getStatus(){
          return this.$store.getters['house/getStatus'];
        }
      }
   }
</script>

<style scoped>

    .am-panel-default > .am-panel-hd{

        background: #fff;
    }
    .am-panel-hd,
    .am-panel-bd,
    .am-panel-default{
        border-color: #5085ff5e;
        border-radius:6px;
    }
    .am-panel-hd{
        padding-top: 0 !important;
        padding-left: 0 !important;
        padding-right: 0 !important;

    }
    .am-panel-title{
        display: block;
        cursor: pointer;
        padding: 0.5rem;
        transition:0.3s;

    }
    .am-panel-title:hover {
        color: #5085FF;
        background: #f0f0f0;
    }
    .am-panel-bd{
        border-top: 1px solid #5085ff1e !important;
        background: #ffffff;
    }
    .am-panel-hd.pft span:hover .am-icon-angle-up:after,
    .am-panel-hd.pft span:hover .am-icon-angle-down:after{

        color: #5085FF;
        padding-left: 5px;
    }
    .am-panel-hd.pft span:hover .am-icon-angle-down:after{
        content:"";
    }
    .am-panel-hd.pft span:hover .am-icon-angle-up:after{
        content:"";
    }

    .am-panel-hd.pft{
        color: #b5b5b5;
        background-color: #fff;
        padding: 0;
        text-align: center;
        border: 0;
        border-top: 1px solid #5085ff1f;
    }

    .cover-wrap{
        width: 100%;
        display: block;
    }
    .cover-bar{
        width: 100%;
        padding-top: 66.6%;
    }
    .cover-img{
        position:absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
    }
    .hd-text{
        padding-left: 10px;
        padding-top: 3px;
        font-size: 16px;
        line-height: 24px;
        color:#333;
    }
    .hd-text a{
        margin-top: 4px !important;
        margin-right: 10px !important;
    }
    .status-tip{
        padding-top: 10px;
    }
</style>



